import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import * as moment from 'moment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public showHome = true;
  public showYou = false;
  public showUs = false;
  public showIndex = true;
  public lignNum = ['0', '1', '2', '3', '4'];
  public colNum = ['0', '1', '2'];
  public imgUrl = 'images/';
  public imgPng = '.png';
  public showYears = true;
  public showDetail = false;
  public years = moment().diff(moment('2008-09-01'), 'years', true);
  public days = moment().diff(moment('2008-09-01'), 'days');

  public chooseHome() {
    this.showHome = true;
    this.showYou = false;
    this.showUs = false;
  }

  public chooseYou() {
    this.showHome = false;
    this.showYou = true;
    this.showUs = false;
  }

  public chooseUs() {
    this.showHome = false;
    this.showYou = false;
    this.showUs = true;
  }

  public toggle() {
    this.showIndex = !this.showIndex;
  }

  public changeTime() {
    this.showYears = !this.showYears;
  }

  public detail() {
    this.showDetail = !this.showDetail;
  }
}



